<template>
  <div>
    <div class='firstLine'>
      <div @click='toShow'>{{nowCheck}}</div>
      <div @click='getReverse' class='picList' ><img :class="{ 'reverse': isReverse }" tabindex="1" src='../../../public/img/ChapterList.png'></div>
      </div>
      <ul class='chapters'  :class="{'overFlow': isOverFlow}">
        <!--<router-link  :to="{path:'/read', query:{lid:[toListUrl,readUrl,nowChapter]}}">-->
          <li v-for='(chapter,index) in chapters' :key='index' v-show='index>=oldList &&index<newList'>
            <router-link  :to="{path:'/read', query:{lid:[toListUrl,chapter.link,index]}}">
              {{chapter.title}}
              </router-link>
          </li>
          <!--</router-link>-->
      </ul>
      <div v-show='showCheck' class='shadows' @touchmove.stop=''> </div>
      <ul class='checkChapter' v-show='showCheck'>
        <li @click='toCheckChapter(singleChapter,index)' v-for='(singleChapter, index) in checkArray' :key='index'>{{singleChapter}}</li>
      </ul>
  </div>
</template>

<script>
import { EventBus } from '../../assets/Bus'
import axios from 'axios'
export default {
  name: 'ChapterList',
  data () {
    return {
      chapters: '',
      oldList: 0,
      newList: 50,
      isReverse: false,
      isOverFlow: false,
      chapterId: '',
      listUrl: '',
      listNum: 0,
      checkArray: [],
      showCheck: false,
      nowCheck: '1~100',
      toListUrl: '',
      readUrl: '',
      nowChapter: ''
    }
  },
  beforeMount () {
    this.getUrl()
  },
  mounted () {
    this.getInfo()
  },
  methods: {
    getInfo () {
      axios
        .get(this.listUrl)
        .then(this.getText)
    },
    getText (res) {
      this.chapters = res.data.chapters
      this.listNum = res.data.chapters.length
      this.getChapterNums()
      this.getRootList()
    },
    getReverse () {
      this.chapters = this.chapters.reverse()
      this.isReverse = !this.isReverse
    },
    getUrl () {
      //  this.chapterId = this.$route.query.chapterId
      this.listUrl = '/apo/toc/mix?bookId=' + this.$route.query.chapterId
      this.toListUrl = this.$route.query.chapterId
    },
    getChapterNums () {
      EventBus.$emit('getChapter',
        this.listNum
      )
    },
    getRootList () {
      let checkNums = this.listNum / 100
      for (let i = 0; i < checkNums; i++) {
        this.checkArray.push(i * 100 + 1 + '~' + (i + 1) * 100)
      }
    },
    toCheckChapter (res, index) {
      this.nowCheck = res
      this.oldList = index * 100 + 1
      this.newList = (index + 1) * 100
      this.showCheck = !this.showCheck
      this.isOverFlow = !this.isOverFlow
    },
    toShow () {
      this.showCheck = !this.showCheck
      this.isOverFlow = !this.isOverFlow
    }
  }
}
</script>

<style lang="scss" scoped>
.firstLine{
  display: flex;
  justify-content: space-between;
  padding: 0px 10px 0px 10px;
}
.chapters{
    margin:0px 0px 0px 32px;
}
.overFlow{
  overflow: hidden;
  height: 500px;
}
li{
        color: #333;
        font-size: 14px;
        padding: 20px 22px 20px 0px;
        border-top: 1px #f3f3f3 solid;
        background-color: #fff
    }
.picList{
img{
     width: 19px;
 }
.reverse{
    transform:rotate(180deg)
}
}
.checkChapter{
  position: absolute;
  top: 20%;
  bottom: 20%;
  left: 10%;
  right: 10%;
  z-index: 999;
  overflow:scroll;
}
.shadows{
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 998;
  top: 0px;
  background-color: rgba(0,0,0,.4);
  overflow: hidden;
}
.router-link-active {
    text-decoration: none;
  }
a {
    text-decoration: none;
    color: #000;
  }
</style>
