<template>
  <div>
      <div  v-show='loading' class='pageLoad'></div>
      <div class='title'>
      <img class='bimg' :src='books.cover'>
      <div class='txt'>
       <h1>{{books.title}}</h1>
       <div class='author'>{{books.author}}</div>
           <div class='info'> 204</div>
           <div class='infot'></div>
      </div>
       </div>
       <div class='btns'>
           <router-link :to="{path:'/read', query:{lid:[lid,firstChapters,'0']}}"><div class='btn1'></div></router-link>
           <div class='btn'></div>
           <div class='btn'></div>
           </div>
  <div class='content'>
      <p v-on:click="test" v-bind:class="{ hide: ishide}" >{{books.longIntro}}</p>
  </div>
  <router-link  :to="{path:'/read', query:{lid:[lid,lastChapters,nowChapters]}}">
<div class='list'><img src='../../../public/img/clock.png'><div></div><div></div></div>
  </router-link>
<router-link  :to="{path:'/chapter', query:{chapterId:lid}}">
<div class='list'><img src='../../../public/img/clock.png'><div></div><div></div></div>
  </router-link>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'BookContent',
  data () {
    return {
      ishide: true,
      books: [],
      lid: null,
      Url: null,
      chapters: null,
      firstChapters: '23',
      lastChapters: null,
      nowChapters: null,
      loading: true
    }
  },
  beforeMount () {
    this.getInfo()
    this.getUrl()
  },
  mounted () {
    this.getHomeInfo()
  },
  methods: {
    getHomeInfo () {
      axios.all([
        axios.get(this.Url),
        axios.get(this.listUrl)])
        .then(axios.spread(this.getBook))
    },
    getBook (resBooks, resChapter) {
      this.loading = false
      this.books = resBooks.data
      this.chapters = resChapter.data.chapters
      this.firstChapters = resChapter.data.chapters[0].link
      this.lastChapters = resChapter.data.chapters[resChapter.data.chapters.length - 1].link
      this.nowChapters = resChapter.data.chapters.length - 1
    },
    test () {
      this.ishide = !this.ishide
    },
    getInfo () {
      this.lid = this.$route.query.lid
    },
    getUrl () {
      this.Url = '/apo/book/info?bookId=' + this.lid
      this.listUrl = '/apo/toc/mix?bookId=' + this.lid
    }
  }
}

</script>

<style lang="scss" scoped>
@import '../css/element.scss';
.title{
    display: flex;
    flex-direction: row;
.bimg{
    width: 98px;
    height: 130px;
    padding: 15px;

}
.txt{
h1{
    font-size: 18px;
    color: #333;
    padding-top: 25px;
}
.author,.info,.infot{
    font-size: 14px;
    color: #999;
    padding-top: 15px;
}}
}
.btns{
    display: flex;
    justify-content: space-around;
.btn,.btn1{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    border: #00c98d solid 1px;
    width: 86px;
    height: 41px;
    border-radius: 5px;
    color: #00c98d;
}
.btn1{
    color: white;
    background-color: #00c98d;
}
}
p{
    font-size: 14px;
    color: #999;
    padding: 8px;
    line-height: 23px;
}
.hide {
    text-overflow: -o-ellipsis-lastline;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
}
.list{
  display: flex;
  img{
    width: 18px;
    height: 18px;
  }
}
.router-link-active {
    text-decoration: none;
  }
a {
    text-decoration: none;
  }
 .pageLoad{
    position: absolute;
    float: right;
    z-index: 9999;
    text-align: center;
    font-size: 15px;
    line-height: 350px;
    width: 100%;
    height: 100%;
    background-color: white;
    img{
      width: 20px;
      margin:auto;
    }
}
</style>
