<template>
  <div>
      <h3></h3>
      <ul>
          <li v-for='(book,index) in books' :key='book._id' v-show='index<4'>
              <router-link :to="{path:'/book', query:{lid:book._id}}">
      <div class='first'>

              <div class='imgCover'><img  :src='book.cover'/></div>
              <div class='text'>
              <div class='booktitle'>{{book.title}}</div>
              <div class='author'>{{book.author}}</div>
              <div class='bookcontent'>{{book.longIntro}}</div>
              <div class='tag'>
              <span class='bluebtn'></span>
              <span class='greybtn'>53</span>
              <span class='greybtn'></span>
              <span class='greybtn'></span>
              </div>
              </div>

          </div>
          </router-link>
          </li>
        </ul>
      </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'HomeInterest',
  data () {
    return { books: null }
  },
  mounted () {
    this.getHomeInfo()
  },
  methods: {
    getHomeInfo () {
      axios
        .get('/apo/node/info?nodeAlias=rankinglist-female-hot-week')
        .then(response => {
          this.books = response.data.books
        })
    }
  }
}
</script>

