    .imgCover{
        display:flex;
        width: 88px;
        margin-right: 10px;
    }
    .text{
        width: 188px;
        display: flex;
        flex-direction: column;
        height: 117.3px;
        justify-content: space-between;
    }
}
.tag{
    display: flex;
    flex-direction: row;
.bluebtn{
    @include bluebtn;
}
