<template>
  <div>
      <div class='inputNovel'>
          <div class='inputImage'>
              <img class='SearchBook' src='../../../public/img/SearchBook.png'/>
      <input  v-model="message" @keyup.enter="submit" type="text"/>
      <img class='cancelImage' src='../../../public/img/cancel.png'/>
          </div>
      </div>
      <ul>
          <li v-for='(book,index) in searchData' :key='index'>
            <router-link :to="{path:'/book', query:{lid:book._id}}">
              <div class='coverImg'><img :src='book.cover' /></div>
            </router-link>
            <router-link :to="{path:'/book', query:{lid:book._id}}">
              <div class='listBook'>
                  <h4>{{book.title}}</h4 >
                  <div class='author'>{{book.author}}</div>
                  <div class='fourTag'>
                  <div class='greenbtn'>{{book.cat}}</div>
                  <div class='greybtn'>{{book.majorCate}}</div>
                  <div class='greybtn'>{{(book.wordCount/10000).toFixed(1) + ''}}</div>
                  </div>
              </div>
            </router-link>
          </li>
      </ul>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'SearchBody',
  data () {
    return {
      message: null,
      searchData: []
    }
  },
  methods: {
    submit () {
      this.getInfo()
    },
    getInfo () {
      axios
        .get('/apo/book/search?key=' + this.message)
        .then(this.getText)
    },
    getText (res) {
      this.searchData = res.data.books
    }
  }
}
</script>

<style lang="scss" scoped>
.inputNovel{
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 80px;
    background-color: #f0f0f3;
    .inputImage{
        // border: 1px red solid;
        height: 40px;
        width: 90%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: white;
    input{
        border: 0;
        outline:none;
        width: 80%;
    }
    .SearchBook{
        width: 14px;
        height: 14px;
    }
    .cancelImage{
        width: 14px;
        height: 14px;
    }
    }
}
li{
    display: flex;
    flex-direction: row;
    border-bottom: #f0f0f3 solid 1px;
    margin: 0px 0px 10px 19px;
    padding: 10px 0px 10px 0px;
.coverImg{
    img{
        width: 56px;
        padding: 0px 10px 0px 0px;
        }
}
.listBook{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 74px;
h3{
    font-size: 16px;
    color: #333;
}
.author{
    font-size: 12px;
    color: #999
}
.fourTag{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 216px;
    .greenbtn{
    display: flex;
    align-items: center;
    font-size: 10px;
    color: #00c98d;
    border: 1px #00c98d solid;
    padding-right: 4px;
    padding-left: 4px;
    white-space: nowrap;
  }
  .greybtn{
    display: flex;
    align-items: center;
    font-size: 10px;
    color: #999;
    border: 1px #999 solid;
    padding-right: 4px;
    padding-left: 4px;
    white-space: nowrap;
  }
}
}
}
.router-link-active {
    text-decoration: none;
  }
a {
    text-decoration: none;
  }
</style>
