<template>
  <div>
    <f-header>
    <template v-slot:header>{{chapterNums}}
  </template>
    </f-header>
  </div>
</template>

<script>
import FHeader from '../core/header.vue'
import { EventBus } from '../../assets/Bus'
export default {
  name: 'ChapterHeader',
  components: {
    FHeader
  },
  data () {
    return {
      chapterNums: 0
    }
  },
  created () {
    this.getChapter()
  },
  methods: {
    getChapter () {
      EventBus.$on('getChapter',
        (data) => {
          this.chapterNums = data
        }
      )
    }
  }
}
</script>

