<template>
<div>
    <pa-a :books='books'>
    <template v-slot:header>
  </template>
    </pa-a>
</div>
</template>

<script>
import PaA from '../core/pa.vue'
import { EventBus } from '../../assets/Bus'
import axios from 'axios'
export default {
  name: 'HomeRecommand',
  components: {
    PaA
  },
  data () {
    return {
      books: null,
      loading: true
    }
  },
  mounted () {
    this.getHomeInfo()
  },
  methods: {
    getHomeInfo () {
      axios
        .get('/apo/node/info?nodeAlias=rankinglist-male-hot-month')
        .then(response => {
          this.books = response.data.books
          this.loading = 'flase'
          EventBus.$emit('loading', this.loading)
        })
    }
  }

}
</script>

