<template>
  <div>
    <pa :books='books'>
    <template v-slot:header>
  </template>
    </pa>
</div>
</template>

<script>
import Pa from '../core/pa.vue'
import axios from 'axios'
export default {
  name: 'HomeBoy',
  components: {
    Pa
  },
  data () {
    return {
      books: null
    }
  },
  mounted () {
    this.getHomeInfo()
  },
  methods: {
    getHomeInfo () {
      axios
        .get('/apo/node/info?nodeAlias=rankinglist-male-hot-week')
        .then(response => {
          this.books = response.data.books
        })
    }
  }
}
</script>

