<template>
  <div>
    <read-loading :showLoadingBook = isShowLoadingBook></read-loading>
    <div @click='getSetting' class='centerClick'></div>
    <div @click='prePage' class='leftClick'>
    </div>
    <div @click='nextPage' class='rightClick'></div>
    <div ref='yellow' :class='{yellow: true,night: beNight  ,green: beGreen,pink: bePink,black: beBlack,light: beLight}' >
    <ul >
    <li v-for='(tp,index) in eee' :key='index' v-html="tp" v-show='index>=oldPage &&index<newPage'>
    </li>
  </ul>
  </div>
  <div v-show='showSetting' class='setting'>
    <div class='topSeeting'>{{this.chapters[this.nowChapter].title}}
    </div>
    <div class='bottomSetting'>
      <ul>
        <li><div  @click='toListShow' class='icon-1'></div><div></div></li>
        <li @click='isNight'><div class='icon-2'></div><div></div></li>
        <li><div @click='getColor' class='icon-3'></div><div></div></li>
        <li @click='goToBack'><div class='icon-4'></div><div></div></li>
      </ul>
    </div>
    <div v-show='showColor'>
      <ul class='skin'>
        <li></li>
        <li class='colors'>
          <div></div><div  @click='isLight' class='circles circleLight'></div><div @click='isPink' class='circles circlePink'></div><div @click='isGreen' class='circles circleGreen'></div><div @click='isBlack' class='circles circleBlack'></div><div @click='isYellow' class='circles circleYellow'></div>
          </li>
      </ul>
      <div class='arrow'></div>
    </div>
  </div>
  <div v-show='showList' class='chapterList'>
    <div class='bookStatus'>
    <h3>{{bookTitle}}</h3>
    <p> {{listNum}}</p>
    <div @click='getReverse' class='picList' ><img :class="{ 'reverse': isReverse }" tabindex="1" src='../../../public/img/ChapterList.png'></div>
    </div>
    <ul class='chapterLists'>
      <li v-for='(chaptersList,index) in chapters' :key='index' @click='clickChapter(index)'>
        {{chaptersList.title}}
        </li>
    </ul>
  </div>
  <div @click='toListShow' class='clickList' v-show='showList'></div>
  </div>
</template>

<script>
import axios from 'axios'
import ReadLoading from '../core/ReadLoading.vue'
export default {
  name: 'NovelRead',
  components: {
    ReadLoading
  },
  data () {
    return {
      body: 'bbbbbb',
      pages: 1,
      text: [],
      len: 2,
      conc: null,
      eee: [],
      oldPage: 0,
      newPage: 18,
      listUrl: null,
      readUrl: null,
      chapters: [],
      listNum: null,
      nowChapter: 0,
      ifPrePage: false,
      beNight: true,
      beGreen: false,
      beLight: false,
      beBlack: false,
      bePink: false,
      beYellow: false,
      showSetting: false,
      showColor: false,
      bookTitle: ' ',
      isReverse: false,
      showList: false,
      isShowLoadingBook: true
    }
  },
  created () {
    this.getUrl()
  },
  mounted () {
    this.getChapterInfo()
    this.getInfo()
  },
  methods: {
    getInfo () {
      axios
        .get(this.readUrl)
        .then(this.getText)
    },
    getText (res) {
      this.oldPage = 0
      this.newPage = 18
      this.eee = []
      // 
      let text = res.data.chapter.body.split('\n')
      let nums = text.length
      this.pages = res.data.chapter.body.split('').length / 12 / 17
      // console.log(this.pages)
      for (var a = 0; a < nums; a++) {
        if (text[a].length < 3) {
          continue
        }
        let dd = text[a].split('')
        let ind2 = dd.indexOf(' ')
        let ind3 = dd.indexOf('')
        if (ind2 > -1) {
          dd.splice(ind2, 4, 'g', 'g')
        } else if (ind3 > -1) {
          dd.splice(ind3, 4, 'g', 'g')
        } else {
          dd.unshift('g', 'g')
        }
        // 
        let c = Math.floor((text[a].length / 12) + 1)
        for (var b = 0; b <= c; b++) {
          if (b === c) {
            break
          }
          let abcc = dd.slice(b * 12, (b + 1) * 12)
          let ind = abcc.indexOf('g')
          if (ind > -1) {
            abcc.splice(ind, 2, '&emsp;&emsp;')
          }
          let abc = abcc.join('')
          this.eee.push(abc)
          this.isShowLoadingBook = false
        }
      }
      this.conc = this.eee
      if (this.ifPrePage) {
        this.oldPage = (this.pages) * 18
        this.newPage = (this.pages + 1) * 18
      }
    },
    getUrl () {
      this.listUrl = '/apo/toc/mix?bookId=' + this.$route.query.lid[0]
      this.readUrl = '/api/' + this.$route.query.lid[1]
      this.nowChapter = Number(this.$route.query.lid[2])
    },
    getChapterInfo () {
      axios
        .get(this.listUrl)
        .then(this.getChapterText)
    },
    getChapterText (res) {
      this.chapters = res.data.chapters
      this.listNum = res.data.chapters.length
      this.getBookTitle()
    },
    getBookTitle () {
      let bookTitles = this.chapters[0].title.split('')
      let titleDelete = bookTitles.indexOf('')
      bookTitles.splice(titleDelete, 999)
      this.bookTitle = bookTitles.join('')
      // console.log(this.bookTitle)
    },
    nextPage () {
      // console.log(this.oldPage)
      if (this.oldPage > ((this.pages - 1) * 18)) {
        this.nowChapter = this.nowChapter + 1
        this.readUrl = '/api/' + this.chapters[this.nowChapter].link
        this.ifPrePage = false
        this.isShowLoadingBook = true
        this.getInfo()
      } else {
        this.oldPage = this.oldPage + 18
        this.newPage = this.newPage + 18
      }
    },
    prePage () {
      if (this.oldPage === 0) {
        this.readUrl = '/api/' + this.chapters[this.nowChapter - 1].link
        this.nowChapter = this.nowChapter - 1
        this.ifPrePage = true
        this.isShowLoadingBook = true
        this.getInfo()
      } else {
        this.oldPage = this.oldPage - 18
        this.newPage = this.newPage - 18
      }
    },
    isNight () {
      this.beNight = !this.beNight
    },
    isLight () {
      this.bePink = false
      this.beGreen = false
      this.beYellow = false
      this.beLight = true
      this.beBlack = false
    },
    isGreen () {
      this.bePink = false
      this.beGreen = true
      this.beYellow = false
      this.beLight = false
      this.beBlack = false
    },
    isPink () {
      this.bePink = true
      this.beGreen = false
      this.beYellow = false
      this.beLight = false
      this.beBlack = false
    },
    isBlack () {
      this.bePink = false
      this.beGreen = false
      this.beYellow = false
      this.beLight = false
      this.beBlack = true
    },
    isYellow () {
      this.bePink = false
      this.beGreen = false
      this.beYellow = true
      this.beLight = false
      this.beBlack = false
    },
    getSetting () {
      this.showSetting = !this.showSetting
    },
    getColor () {
      this.showColor = !this.showColor
    },
    goToBack () {
      this.$router.go(-1)
    },
    getReverse () {
      this.chapters = this.chapters.reverse()
      this.isReverse = !this.isReverse
    },
    toListShow () {
      this.showList = !this.showList
    },
    clickChapter (index) {
      this.toListShow()
      this.nowChapter = index
      this.readUrl = '/api/' + this.chapters[this.nowChapter].link
      this.isShowLoadingBook = true
      this.getInfo()
    }
  }
}

</script>

